
.pagination{
    display: flex;
    justify-content: center;
    
    flex-flow:  wrap;

   }
 .page-link{
      width:70px;
      text-align: center;
      border:2px rgba(255, 255, 255, 0.2) solid;
     margin: 5px 10px;
     border-radius: 10px;
     background-color: black;
     color:coral;
     transition: 0.2s;
}
.page-link:hover{
     color:white;
     background-color: rgb(114, 206, 223)
}
.active{
     background-color: chartreuse;
     color: black;
}
.form{
       color: azure;
       border-radius: 30px;
       padding: 100px;
       height: 300px;
       min-height: 500px;
       margin-top: 200px;
      z-index: 99;

}


.inputControl{
     display: block;
     margin-top: 30px;
     background-color: #fff;
     color: black;
     border-radius: 10px;
     padding: 2px;
}
label{
     min-width: 100px;
     text-align: center;
}
input{
     padding: 5px;
     border-radius:10px;
     margin-left: 30px;
}
input[type="submit"]{
     margin-top: 30px;
     color: azure;
     padding: 13px;
     background-color: #122;
     transition: 0.3s;
     width: 240px;
}
input[type="submit"]:hover{
     padding: 13px;
     background-color: #452;
}
.loginContainer{
     display:flex;
     justify-content: space-evenly;
     /* animation:fadeIn 1s; */
     position:fixed;
      width:100px;
      height:100px;
      min-width:100%;
      min-height:300%;
      background-color:rgba(41, 79, 104, 0.521);
      
     z-index: 100;

}
.loginBgGif{
     width: 40%;
     height: 20%;
     z-index: 9;
     margin-top: 100px;
     position: relative;
}